	module.exports = {"data":{"post":{"html":"<p>Der Schockwellenreiter <a href=\"http://www.schockwellenreiter.de/2002/09/03.html#a7586\">comments</a> on <a href=\"http://www.fogcreek.com/CityDesk/\">City Desk</a> (freely translated into American English, the only language Der Schockwellenreiter does not endorse  :-) </p>\n<p>\"[ CMS ]:  And if one follows the link to City Desk, one discovers an interesting content management system (on the Desktop) similar to Radio Userland, with several new ideas. Unfortunately it's only for Windows (it's based on Micro$ofts Jet engine).  And why they needed yet introduce yet another tag system ({ $. tag $ } instead of < % day % >) remains the secret of the programmer.\"</p>\n<p>True enough, but you can put City Desk on the desktop of inexperienced PC users. And our <em>Tagesmutter</em> thinks it's <a href=\"http://www.tagesmuetter-vaeter.de/\">wonderful</a>. I would have never <em>dreamt</em> of giving her <a href=\"http://radio.userland.com\">Radio Userland</a>.</p>","fields":{"title":"City Desk","date":"03 Sep 2002","url":"https://papascott.de/2002-09-03-city-desk/"}}},"pathContext":{"slug":"/2002-09-03-city-desk/","prev":{"fields":{"title":"Sending Out an SMS","date":"02 Sep 2002","slug":"/2002-09-02-sending-out-an-sms/"}},"next":{"fields":{"title":"Anniversary of Terror","date":"05 Sep 2002","slug":"/2002-09-05-anniversary-of-terror/"}}}}

